import React from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import Typography from "../Typography";
import Avaliation from "../Avaliation";
import Logo from "../assets/img/tripadvisor.svg";
import { styles } from "./styles";

const TripAdvisor = ({ classes, rate, className, totalAvaliations }) => {
    let label = "Excelente!";

    if (rate > 8) {
        label = "Excelente!";
    }
    if (rate <= 8) {
        label = "Muito Bom!";
    }
    if (rate <= 6) {
        label = "Intermediário";
    }
    if (rate <= 4) {
        label = "Ruim";
    }
    if (rate <= 2) {
        label = "Muito Ruim";
    }

    return (
        <div className={`${className} ${classes.trip}`}>
            <div className={classes.rate}>
                <Avaliation label={rate} />
                <div className={classes.label}>
                    <Typography type="body2semibold">{label}</Typography>
                    <Typography type="body2regular">
                        {totalAvaliations} Avaliações
                    </Typography>
                </div>
            </div>
            <div>
                <img src={Logo} alt="tripadvisor" />
            </div>
        </div>
    );
};

TripAdvisor.propTypes = {
    classes: PropTypes.object.isRequired,
    className: PropTypes.string,
    totalAvaliations: PropTypes.string.isRequired,
    rate: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
};

export default withStyles(styles, { withTheme: true })(TripAdvisor);
